import React from 'react';
import {
    Panel,
    PanelHeader,
    IOS,
    platform,
    PanelHeaderButton,
    Group,
    Div,
    Header,
    List,
    Cell,
    Button
} from '@vkontakte/vkui';

import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

import tovar1 from '../img/tovar1.jpg';
import tovar6 from '../img/tovar6.jpg';

const osname = platform();

class TestActiveTovar extends React.Component {
    constructor (props) {
        super(props);

        this.state = {};
    }

render () {
    const {
        id,
        go,
    } = this.props;

return (
    <Panel id={id} separator={true}>
        <PanelHeader
            left={
                <PanelHeaderButton onClick={go} data-to="orders">
                    {osname === IOS ? <Icon28ChevronBack /> : <Icon24Back />}
                </PanelHeaderButton>
            }
        >
            Заказ #1234
        </PanelHeader>
            <Group header={<Header mode="secondary">Дата заказа</Header>}>
                <Div>
                    25.05.2020 в 5:20
                </Div>
            </Group>
            <Group header={<Header mode="secondary">Статус</Header>}>
                <Div>
                    На рассмотрении
                </Div>
            </Group>
            <Group header={<Header mode="secondary">Товары</Header>}>
                <List>
                    <Cell
                        onClick={go} data-to="testTovar"
                        before={
                            <div className="ico-align">
                                <img
                                    className="ico-crop"
                                    type="image"
                                    src={tovar1}
                                />
                            </div>
                        }
                        multiline
                    >
                        <span>MacBook Air</span>
                        <span className="disable-selectionn text">
                            Цена: 
                            4100 баллов
                        </span>
                        <span className="disable-selectionn text">
                            Категория:
                            Девайсы
                        </span>
                        <span className="disable-selectionn text">
                            Количество:
                            x1
                        </span>
                    </Cell>

                    <Cell
                        onClick={go} data-to="testTovar2"
                        before={
                            <div className="ico-align">
                                <img
                                    className="ico-crop"
                                    type="image"
                                    src={tovar6}
                                />
                            </div>
                        }
                        multiline
                    >
                        <span>Футболка</span>
                        <span className="disable-selectionn text">
                            Цена: 
                            150 баллов
                        </span>
                        <span className="disable-selectionn text">
                            Категория:
                            ÖMANKÖ for VK Testers
                        </span>
                        <span className="disable-selectionn text">
                            Количество:
                            x1
                        </span>
                    </Cell>
                </List>
            </Group>
            <Group header={<Header mode="secondary">Способ доставки</Header>}>
                <Div>Доставка</Div>
            </Group>
            <Group header={<Header mode="secondary">Контактный телефон</Header>}>
                <Div>+7 123 456 78 90</Div>
            </Group>
            <Group header={<Header mode="secondary">Адрес</Header>}>
                <Div>Россия, Москва, ул. Пушкина, д. 123, кв. 4, корпус 5, индекс: 678910</Div>
            </Group>
            <Group header={<Header mode="secondary">Получатель</Header>}>
                <Div>Тихонович Александр Дмитриевич</Div>
            </Group>
            <Group header={<Header mode="secondary">Детали заказа</Header>}>
                <Div>-</Div>
            </Group>
            <Group>
                <Div><Button size="xl" mode="secondary">Отменить заказ</Button></Div>
            </Group>
        </Panel>
        );
    }
}

export default TestActiveTovar;